@import 'shared/base';


/*
** Layout
*/

body {
	background-color: #e5e5e5;
}

section {
	position: relative;
	margin: 0 auto;
	padding: 4rem 1rem 3rem;

	background-color: #fff;
	border-bottom: 2px solid #ff5722;

    .section-anchor {
        position: absolute;
        top: 0;
        visibility: hidden;
    }

    ul {
        padding-left: 0;
        list-style: none;
        margin-bottom: 2rem;

        &:last-child {
            margin-bottom: 0;
        }
    }
    
    li {
        position: relative;
        padding-left: 20px;

        &:before {
            content: '';
            display: block;
            width: 6px;
            height: 6px;
            position: absolute;
            top: 6px;
            left: 3px;

            background-color: #90a4ae;
            border-radius: 50%;
        }

        & + li {
            margin-top: 0.5rem;
        }
    }
}

@media screen and (min-width: 480px) {
	section {
		padding-left: 2rem;
		padding-right: 2rem;
	}
}

@media screen and (min-width: 768px) {
	section {
		max-width: 600px;
		padding: 5rem 2rem 2rem;
		margin-bottom: 4rem;

		opacity: 0.75;
		transition: opacity 0.5s, box-shadow 0.5s;
	}

	.section-raised {
		opacity: 1;
		box-shadow: 0 10px 50px 2px rgba(0,0,0,0.07);
	}
}



/*
** Typography
*/

h1 {
	margin: 5rem 1rem;

	font-family: 'Roboto Slab', serif;
	font-size: 2.25rem;
	font-weight: 300;
	color: #607d8b;
	text-align: center;
}

h2 {
	position: absolute;
	width: 100%;
	top: 0;
	left: 0;
	z-index: 1;
	padding: 1rem;
	margin: 0;

	font-size: 2rem;
	font-weight: 300;
	color: #ff5722;
}

.h2-floating {
	position: fixed;
	top: 0;
	left: auto;
	width: 100%;
	margin-left: -1rem;

	background-color: #fff;
	border-bottom: 1px solid rgba(0,0,0,0.1);
}

.h2-bottom {
	top: auto;
	bottom: 0;
	left: 0;
	right: 0;
	width: auto;

	background-color: #fff;
}

h3 {
	margin: 1rem 0;

	font-size: 1.25rem;
	font-family: 'Roboto Slab', serif;
	color: #37474f;
}

@media screen and (min-width: 480px) {
	h2 {
		padding: 1rem 2rem;
	}

	.h2-floating {
		margin-left: -2rem;
	}
}

@media screen and (min-width: 768px) {
	h1 {
		font-size: 2.75rem;
	}

	h2 {
		font-size: 2.5rem;
	}

	.h2-floating {
		max-width: 600px;
	}
}



/*
** Links
*/

a {
    padding: 1px 0;

    border-radius: 2px;
    color: #607d8b;
    text-decoration: none;
    transition: background-color 0.25s, box-shadow 0.25s;
}

a:hover,
a:focus {
    background-color: rgba(0,0,0,0.06);
    box-shadow: 4px 0 0 rgba(0,0,0,0.06), -4px 0 0 rgba(0,0,0,0.06);
}

a:focus {
    outline: none;
}
